<template>
  <div id="main">
      <div class="header">
          <h1 class="title">Directors</h1>
      </div>
      <v-dialog />
      <router-link to="/director" tag="button" class="button is-primary">Add Director</router-link>

      <div class="content">

          <table id="directors" class="table">
              <thead>
                  <tr>
                      <th>#</th>
                      <th>Name</th>
                      <th>Date of Birth</th>
                      <th></th>
                  </tr>
              </thead>
              <tbody>
                  <tr v-for="director in directors" :key="director.id">
                      <td>{{ director.id }}</td>
                      <td>{{ director.firstName }} {{ director.lastName }}</td>
                      <td>{{ moment(director.dateOfBirth).utc().format("MM/DD/YYYY") }}</td>
                      <td> 
                        <a @click="deleteDirector(director.id)" class="is-secondary"> Delete</a>
                        <router-link :to="'/director/' + director.id" tag="button" class="button is-secondary">Edit</router-link>
                      </td>
                  </tr>
              </tbody>
          </table>

      </div>

  </div>
</template>

<script>
export default {
  name: 'Directors',
  data: () => ({
      directors: []
  }),
  methods: {
    async deleteDirector(id) {
            try {
                await this.$http.delete(`http://localhost:8080/api/directors/${id}`)
                const { data } = await this.$http.get('http://localhost:8080/api/directors');
                this.directors = data;
            } catch (e) {
                this.$modal.show('dialog', {
                    title: 'Error',
                    text: 'Deleting Director failed',
                    buttons: [
                        {
                            title: 'Ok',
                            handler: () => {
                                this.$modal.hide('dialog')
                            }
                        }
                    ]
                })
            }
        }
  },

  async mounted() {
      console.log('directors mounted being');
      const { data } = await this.$http.get('http://localhost:8080/api/directors');
      console.log('directors mounted data', data);
      this.directors = data;
  }
}
</script>

<style scoped>
  button {
      float: right;
  }
</style>