<template>
    <div id="main">
        <div class="header">
            <h1 class="title">Actors</h1>
        </div>
        <v-dialog />
        <router-link to="/actor" tag="button" class="button is-primary">Add Actor</router-link>

        <div class="content">

            <table id="actors" class="table">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>Name</th>
                        <th>Date of Birth</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="actor in actors" :key="actor.id">
                        <td>{{ actor.id }}</td>
                        <td>{{ actor.firstName }} {{ actor.lastName }}</td>
                        <td>{{ moment(actor.dateOfBirth).utc().format("MM/DD/YYYY") }}</td>
                        <td> 
                            <a @click="deleteActor(actor.id)" class="is-secondary"> Delete</a>
                            <router-link :to="'/actor/' + actor.id" tag="button" class="button is-secondary">Edit Actor</router-link>
                        </td>

                    </tr>
                </tbody>
            </table>

        </div>

    </div>
</template>

<script>
export default {
    name: 'Actors',
    data: () => ({
        actors: []
    }),
    methods: {
        async deleteActor(actorId) {
            try {
                await this.$http.delete(`http://localhost:8080/api/actors/${actorId}`)

                this.$modal.show("example")
                const { data } = await this.$http.get('http://localhost:8080/api/actors');
                this.actors = data;
            } catch (e) {
                this.$modal.show('dialog', {
                    title: 'Error',
                    text: 'Deleting Actor failed',
                    buttons: [
                        {
                            title: 'Ok',
                            handler: () => {
                                this.$modal.hide('dialog')
                            }
                        }
                    ]
                })
            }
        }
    },
    async mounted() {
        console.log('actors mounted being');
        const { data } = await this.$http.get('http://localhost:8080/api/actors');
        console.log('actors mounted data', data);
        this.actors = data;
    }
}
</script>

<style scoped>
button {
    float: right;
}

.container {
    font-family: arial;
    font-size: 24px;
    margin: 25px;
}

p {
    /* Center horizontally*/
    text-align: center;
    color: red;
}
</style>